.photos-container {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 80px);
  width: 100%;
  padding: 60px 40px;
  background-color: #eef0f2;
}

.photos-header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 100%;
}

.header {
  font-weight: normal;
  font-size: 36px;
  border-bottom: 1px solid black;
}

.photos-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  min-height: 100%;
  width: 100%;
}

.photos-content-div {
  position: relative;
  overflow: hidden;
  width: 25%;
  height: 400px;
  margin: 6px;
  padding: 10px 0;
  border: 1px solid black;
  opacity: 1;
  transition: 0.6s all ease-in-out;
}

.photos-content-div-overflow {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  overflow: hidden;
  z-index: 2;
  transition: 0.6s;
  top: 0;
  left: 100%;
  width: 40%;
  height: 100%;
  padding: 10px 15px;
  background-color: white;
}

.photos-content-div:hover .photos-content-div-overflow {
  transform: translateX(-100%);
}

.photos-p {
  display: none;
  font-size: 48px;
}

.photos-content-div:hover {
  transition: 0.7s ease-in-out;
}

.photos-content-header {
  font-size: 24px;
  text-align: center;
}

.photos-content-p {
  margin-top: 20px;
  font-size: 16px;
  text-align: center;
}

.photos-content-div:nth-child(1) {
  background-image: url(../../assets/images/jag-backround.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.photos-content-div:nth-child(2) {
  background-image: url(../../assets/images/passat-background.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 1080px) {
  .photos-content {
    flex-direction: column;
  }
  .photos-content-div {
    width: 90%;
  }
  .photos-content-p {
    font-size: 12px;
  }
  .photos-content-header {
    font-size: 18px;
  }

  .photos-container {
    padding: 10px 5px;
  }
}
